#{$appPrefix} {
    &-ctrl-aud {
        &-slf-hosted {
            #{$varPrefix}-m: 0 0 15px 0;

            #{$appPrefix}-btn {
                &-uploads {
                    #{$varPrefix}-gap   : 0 5px;
                    #{$varPrefix}-p     : 10px 7px;
                    #{$varPrefix}-fnt-sz: 14px;
                    #{$varPrefix}-w     : calc((100% / 2) - 8px);


                    .at-svg {
                        #{$varPrefix}-w: 13px;
                        #{$varPrefix}-h: 13px;
                    }
                }
            }
        }

        #{$appPrefix} {

            &-select-multi {
                flex-grow: 1;
            }
        }
    }
}